<template>
  <div class="topic-list">
    <!--  搜索  -->
    <el-form
      ref="babyForm"
      label-position="left"
      label-width="90px"
      :model="babyForm"
      inline
    >
      <el-form-item label="宝宝乳名" class="search-item" prop="name">
        <el-input v-model="babyForm.name" placeholder="请输入宝宝乳名" />
      </el-form-item>
      <el-form-item label="套餐名称" class="search-item" prop="package">
        <service-package v-model="babyForm.package"></service-package>
      </el-form-item>
      <el-form-item label="用户" class="search-item" prop="user">
        <el-input
          v-model="babyForm.user"
          placeholder="请输入用户昵称或备注名"
        />
      </el-form-item>
      <el-form-item label="宝宝ID" class="search-item" prop="babyId">
        <el-input v-model="babyForm.babyId" placeholder="请输入宝宝ID" />
      </el-form-item>
      <el-form-item label="用户ID" class="search-item" prop="userId">
        <el-input
          v-model="babyForm.userId"
          placeholder="请输入用户ID"
          style=" margin-right: 70px;width: auto;"
        />
        <el-button
          type="primary"
          class="select-btn"
          @click="getBabyList(babyForm, 'search')"
          >搜索</el-button
        >
        <el-button
          type="primary"
          class="select-btn"
          @click="resetForm('babyForm')"
          >重置</el-button
        >
      </el-form-item>
    </el-form>
    <!--  列表  -->
    <div class="list">
      <div class="title">
        <div>签约宝宝列表（服务中）</div>
      </div>
      <!-- :data="list" -->
      <el-table style="width: 100%" :empty-text="hasListInfo" :data="list">
        <el-table-column
          fixed
          prop="num"
          label="序号"
          min-width="60"
          align="center"
        />
        <el-table-column label="宝宝" min-width="100" align="center">
          <template slot-scope="scope">
            <div style="text-align: center">
              <div>{{ scope.row.name }}</div>
              <div>{{ scope.row.babyId }}</div>
            </div>
          </template>
        </el-table-column>
        <el-table-column
          prop="user"
          label="用户"
          min-width="150"
          align="center"
        >
          <template slot-scope="scope">
            <div class="user-avatar">
              <el-avatar :src="scope.row.avatar" :size="28" />{{
                scope.row.user
              }}
              <svg-icon v-if="scope.row.is_replace" icon-class="replace" />
            </div>
            <div>{{ scope.row.userId }}</div>
          </template>
        </el-table-column>
        <el-table-column label="出生日期" min-width="100" align="center">
          <template slot-scope="scope">
            {{ new Date(scope.row.birthday) | formatTime('y-m-d') }}
          </template>
        </el-table-column>
        <el-table-column
          prop="monthAge"
          label="月龄"
          min-width="100"
          align="center"
        />
        <el-table-column prop="babyNote" min-width="120" align="center">
          <template slot="header">
            宝宝备注
            <el-tooltip
              class="item"
              effect="dark"
              content="接诊台宝宝资料处添加的备注"
              placement="top"
            >
              <i class="el-icon-info" />
            </el-tooltip>
          </template>
          <template slot-scope="scope">
            <span class="baby-note">{{ scope.row.babyNote }}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="servicePackage"
          label="当前服务套餐"
          min-width="120"
          align="center"
        />
        <el-table-column
          prop="serviceTime"
          label="当前服务时间"
          min-width="120"
          align="center"
        >
          <template slot-scope="scope">
            {{ new Date(scope.row.started_at) | formatTime('y-m-d') }}
            至 {{ new Date(scope.row.ended_at) | formatTime('y-m-d') }}
          </template>
        </el-table-column>
        <el-table-column fixed="right" label="操作" width="100" align="center">
          <template slot-scope="scope">
            <el-button
              type="text"
              size="small"
              @click="$router.push(`/health-file/file-detail/${scope.row._id}`)"
              >档案详情</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <div class="pagination">
        <el-pagination
          :current-page="currentPage"
          :page-sizes="[12, 18, 24]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="count"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { healthBabyList } from '@/api/healthManagement'
import ServicePackage from '@/components/SearchItem/ServicePackage'
export default {
  data() {
    return {
      list: [],
      dataList: [],
      currentPage: 1,
      pageSize: 12,
      count: 0,
      hasListInfo: '暂无数据',
      singleList: [],
      createNameList: [],
      babyForm: {
        name: '',
        package: '',
        user: '',
        userId: '',
        babyId: ''
      }
    }
  },
  components: {
    ServicePackage
  },
  created() {
    this.getBabyList(this.babyForm)
  },
  methods: {
    handleSizeChange(val) {
      this.pageSize = val
      this.getBabyList(this.babyForm)
    },
    handleCurrentChange(val) {
      this.currentPage = val
      this.getBabyList(this.babyForm)
    },
    async getBabyList(search, type) {
      if (type === 'search') {
        this.currentPage = 1
      }
      const params = {
        current_page: this.currentPage,
        page_size: this.pageSize,
        service_item: 'health_record',
        show_replace: 1,
        type: 'erke'
      }
      if (search.name) params.baby_name = search.name
      if (search.user) params.user_name = search.user
      if (search.package) params.fd_service_type = search.package
      if (search.userId) params.user_id = search.userId
      if (search.babyId) params.baby_id = search.babyId
      const { data, code } = await healthBabyList(params)
      if (code === 20000) {
        this.count = data.count
        if (data.list) {
          this.list = data.list.map((item, index) => {
            return {
              num: item.index,
              name: item.name,
              user: item.user_info.nickname,
              birthday: this.formatTime(item.birthday),
              monthAge: item.age_of_month,
              babyNote: item.remark || '宝宝备注',
              servicePackage: item.fd_service_type_info.name,
              userId: item.user_info._id,
              avatar: item.user_info.avatar,
              _id: item._id,
              id: item.id,
              babyId: item.babyId,
              started_at: item.fd_contract_info.started_at,
              ended_at: item.fd_contract_info.ended_at,
              is_replace: item.is_replace
            }
          })
        }
      }
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.getBabyList(this.babyForm, 'search')
    }
  }
}
</script>

<style lang="scss" scoped>
.topic-list {
  display: flex;
  flex-wrap: wrap;
  font-size: 14px;
  margin-bottom: 20px;
  /deep/form {
    width: 100%;
    border-bottom: 1px dotted #ebeef5;
    .el-date-editor {
      padding: 0 10px;
      width: 220px;
      height: 32px;
      .el-range-input {
        width: 85px;
        height: 30px;
      }
    }
  }
  .search-item {
    margin-right: 70px;
    color: #17233d;
    /deep/input {
      width: 220px;
      height: 32px;
    }
    /deep/span {
      top: -3px;
    }
    /deep/.el-input__suffix {
      top: 0;
    }
  }
  .select-btn {
    padding: 0 20px;
    width: 68px;
    height: 32px;
  }
  .arrow {
    float: right;
  }
  .list {
    margin: 20px 0;
    padding: 20px;
    width: 100%;
    .title {
      display: flex;
      justify-content: space-between;
      padding-left: 20px;
      line-height: 50px;
      font-size: 14px;
      font-weight: 500;
      background-color: #f2f6fc;
      /deep/button {
        padding: 6px 20px;
        height: 32px;
      }
    }
    /deep/.el-dialog__wrapper {
      button {
        padding: 6px 20px;
        height: 32px;
      }
      .el-dialog__headerbtn {
        top: 10px;
        right: 5px;
      }
    }
    .pagination {
      margin-top: 50px;
      text-align: center;
    }
  }
  .center {
    text-align: center;
  }
  .user-avatar {
    display: inline-block;
    .el-avatar {
      vertical-align: middle;
    }
  }
  .baby-note {
    display: -webkit-box;
    overflow: hidden;
    max-height: 2.4em;
    line-height: 1.2em;
    text-overflow: ellipsis;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
  }
}
</style>
